        rgb(58, 29, 69, 0.9),
        rgb(30, 24, 44, 0.9),
        rgb(12, 12, 14, 0.9)
      ), url(/static/media/30577992388_b5e447777e_o.979ab808.jpg);
    background: radial-gradient(
        circle at bottom,
        rgb(139, 0, 91, 0.9),
        rgb(96, 26, 85, 0.9),
        rgb(58, 29, 69, 0.9),
        rgb(30, 24, 44, 0.9),
        rgb(12, 12, 14, 0.9)
      ), url(/static/media/30577992388_b5e447777e_o.979ab808.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

