#scrolldown {
    font-family: "VT323", monospace;
    color: #d90474;
    text-shadow: 2px 2px #00bfa8;
    margin-top: 3em;
  
    font-size: 16px;
    font-weight: 900;
    text-decoration: none;
    color: white;
  
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    -ms-background-clip: text;
    -ms-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    background-image: -webkit-linear-gradient(45deg, #7794ff, #44107a, #ff1361, #fff800);
    background-image: linear-gradient(45deg, #7794ff, #44107a, #ff1361, #fff800);
    -webkit-animation: 0.8s shake infinite alternate;
    animation: 0.8s shake infinite alternate;
  }
  
  @-webkit-keyframes shake {
    0% {
      -webkit-transform: skewX(-15deg);
              transform: skewX(-15deg);
    }
    5% {
      -webkit-transform: skewX(15deg);
              transform: skewX(15deg);
    }
    10% {
      -webkit-transform: skewX(-15deg);
              transform: skewX(-15deg);
    }
    15% {
      -webkit-transform: skewX(15deg);
              transform: skewX(15deg);
    }
    20% {
      -webkit-transform: skewX(0deg);
              transform: skewX(0deg);
    }
    100% {
      -webkit-transform: skewX(0deg);
              transform: skewX(0deg);
    }
  }
  
  @keyframes shake {
    0% {
      -webkit-transform: skewX(-15deg);
              transform: skewX(-15deg);
    }
    5% {
      -webkit-transform: skewX(15deg);
              transform: skewX(15deg);
    }
    10% {
      -webkit-transform: skewX(-15deg);
              transform: skewX(-15deg);
    }
    15% {
      -webkit-transform: skewX(15deg);
              transform: skewX(15deg);
    }
    20% {
      -webkit-transform: skewX(0deg);
              transform: skewX(0deg);
    }
    100% {
      -webkit-transform: skewX(0deg);
              transform: skewX(0deg);
    }
  }

  #press-to-enter{
    padding: 1em;
    font-size: 1.5em;
    background-image: -webkit-linear-gradient(bottom,#e3b943,#e3ca4b,#e2dc56,#deed63,#d9ff74);
    background-image: linear-gradient(0deg,#e3b943,#e3ca4b,#e2dc56,#deed63,#d9ff74);
    box-shadow: 1px 3px #d90474;
    border-radius: 1.6em;
    font-weight: bolder;
    color: #3f1942;
    text-decoration: none;
    font-family: VT323,monospace;
    text-shadow: 2px 2px #00bfa8;
  }

  #press-to-enter:hover{
    cursor: pointer;
    color: #3f1942;
    background-image: -webkit-linear-gradient(
      top,
      #e3b943,
      #e3ca4b,
