      #deed63,
      #d9ff74
    );
    background-image: linear-gradient(
      to bottom,
      #e3b943,
      #e3ca4b,
      #e2dc56,
      #deed63,
      #d9ff74
    );
  }

  @media screen and (max-width: 768px){
